import Mock from 'mockjs'
export function mockUser () {
  Mock.mock('/api/user', {
  //  list  4  6 
    'peoples|4-6': [{
      'department': '',
      //  list  4  6 
      'peopleInfo|4-6': [{
        //  id  1 1
        'user_id|+1': 1,
        'user_name': '@cname',
        'job_name': ''
      }]
    }]
  })
}
